.photo-list-item {
  list-style: none;
  height: 325px;
  margin-left: 15px;
}

.photo-list-item img {
  height: 325px;
  width: 257px;
  -o-object-fit: cover;
     object-fit: cover;
}

.photo-ul {
  -webkit-padding-start: 0px;
  display: -ms-flexbox;
  display: flex;
  width: 1218px;
  position: absolute;

}

.photo-ul li:first-child {
  margin-left: 0px;
}

.slideInRight {
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-name: rightSlideIn;
          animation-name: rightSlideIn;
}

.slideOutRight {
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-name: rightSlideOut;
          animation-name: rightSlideOut;
}

.slideInLeft {
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-name: leftSlideIn;
          animation-name: leftSlideIn;
}

.slideOutLeft {
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-name: leftSlideOut;
          animation-name: leftSlideOut;
}

@-webkit-keyframes rightSlideIn {
  0% {
    margin-left: 100%;
  }
  100% {
    margin-left: 0%;
  }
}

@keyframes rightSlideIn {
  0% {
    margin-left: 100%;
  }
  100% {
    margin-left: 0%;
  }
}

@-webkit-keyframes rightSlideOut {
  0% {
    margin-left: 0%;
  }
  100% {
    margin-left: -100%;
  }
}

@keyframes rightSlideOut {
  0% {
    margin-left: 0%;
  }
  100% {
    margin-left: -100%;
  }
}

@-webkit-keyframes leftSlideIn {
  0% {
    margin-left: -100%;
  }
  100% {
    margin-left: 0%;
  }
}

@keyframes leftSlideIn {
  0% {
    margin-left: -100%;
  }
  100% {
    margin-left: 0%;
  }
}

@-webkit-keyframes leftSlideOut {
  0% {
    margin-left: 0%;
  }
  100% {
    margin-left: 100%;
  }
}

@keyframes leftSlideOut {
  0% {
    margin-left: 0%;
  }
  100% {
    margin-left: 100%;
  }
}

@media only screen and (max-width: 1200px) {
  .photo-ul {
    width: 992px;
  }

  .photo-list-item img {
    width: 206px;
    height: auto;
  }
}

@media only screen and (max-width: 992px) {
  .photo-ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    width: 544px;
    -webkit-margin-before: 0em;
    -webkit-margin-after: 0em;
  }

  .photo-list-item {
    margin-top: 5px;
  }
  
  .photo-list-item img {
    width: 257px;
    height: auto;
  }

  .photo-ul li:first-child {
    margin-left: 15px;
  }

}




@media only screen and (max-width: 320px) {
  .photo-ul {
    width: 524px;
  }

  .photo-list-item {
    margin-left: 5px;
    height: 218;
  }

  .photo-list-item img {
    width: 170px;
  }
}

@media only screen and (max-width: 480px) {
  .photo-list-item img {
    width: 203px;
  }
  .photo-list-item {
    height: 270px;
  }
}



